import Footer from "components/navigation/Footer"
import Navbar from "components/navigation/Navbar"
import Header from "components/home/Header"
import Noticias from "components/home/Noticias"
import Contacto from "components/home/Contacto"
import Layout from "hocs/layouts/Layout"
import Conexionfd from "components/home/Conexionfd"
import Services from "components/home/Services"
import Trends from "components/home/Trends"
import { connect } from "react-redux";
import { useEffect } from "react"
import { get_news, get_news_home } from "redux/actions/news/news"



function Home({ get_news, get_news_home, newshome }) {
    

    useEffect(() => {
        get_news()
        get_news_home()

    }, [])

    return (
        <Layout>
            <Navbar />
            <Header />
            {newshome ? <>
                <Noticias newshome={newshome&&newshome} />
          
        

                </>:
                <>loading</>
            }
    
            <Trends /> 
            <Services />
       
            <Conexionfd />
            <Footer />
        </Layout>
    )
}
const mapStateToProps = state => ({

    news: state.news,
    newshome:state.news.newshome


})

export default connect(mapStateToProps, {

    get_news, get_news_home

})(Home)